<template>
  <div class="registration-modal">
    <vue-modal-switcher :value="switcherQuestionParams">
      <p>Вы проживаете в городе Владивосток/Пригород?</p>
      <vue-switcher v-model="switcherQuestionParams" />
      <p v-if="switcherQuestionParams[1].isSwitch">
        Мы работам только во Владивостоке и в пригороде!
      </p>
    </vue-modal-switcher>
  </div>

  <div class="registration-page" v-if="switcherQuestionParams[0].isSwitch">
    <div class="registration-page__title">
      <div class="registration-page__titles">
        <h2
          @click="selectTypeRegistration(loginType)"
          :class="{ active_login_type: loginType === 'registration' }"
        >
          регистрация
        </h2>
        <h2
          :class="{ active_login_type: loginType === 'login' }"
          @click="selectTypeRegistration(loginType)"
        >
          вход
        </h2>
      </div>
    </div>

    <vue-form
      class="registration-page__form"
      v-if="loginType === 'registration'"
    >
      <div class="form--switcher__role">
        <label class="form--switcher__label">Кем хотите работать?</label>
        <vue-switcher v-model="switcherRoleParams" />
      </div>
      <div class="form-bio">
        <div class="form-bio__firstName">
          <label>Фамилия</label>
          <vue-text-input
            v-model="registrationInfo.userFirstName.firstName.value"
            v-model:is-valid="registrationInfo.userFirstName.isValid.value"
            :placeholder="'Фамилия'"
          />
        </div>
        <div class="form-bio__name">
          <label>Имя</label>
          <vue-text-input
            v-model="registrationInfo.userName.name.value"
            v-model:is-valid="registrationInfo.userName.isValid.value"
            :placeholder="'Имя'"
          />
        </div>
        <div class="form-bio__serName">
          <label>Отчество</label>
          <vue-text-input
            v-model="registrationInfo.userSerName.serName.value"
            v-model:is-valid="registrationInfo.userSerName.isValid.value"
            :placeholder="'Отчество'"
          />
        </div>
      </div>
      <div class="form--number__input">
        <label class="form--number__label">Номер телефона</label>
        <vue-number-input
          v-model="registrationInfo.userPhone.phone.value"
          v-model:is-valid="registrationInfo.userPhone.isValid.value"
          :type="'phone'"
          :placeholder="'+7 (###)-###-##-##'"
          :number-params="registrationInfo.userPhone"
        />
      </div>

      <div class="form--number__input">
        <label class="form--number__label">Номер whatsapp</label>
        <vue-number-input
          v-model="registrationInfo.userWhatsapp.phone.value"
          v-model:is-valid="registrationInfo.userWhatsapp.isValid.value"
          :type="'whts'"
          :placeholder="'+7 (###)-###-##-##'"
        />
      </div>

      <div class="form--date__input" v-if="userRole === 1">
        <label class="form--date__label">Дата рождения</label>
        <vue-number-input
          v-model="registrationInfo.userDate.date.value"
          v-model:is-valid="registrationInfo.userDate.isValid.value"
          :placeholder="'дд.мм.гггг'"
          :type="'date'"
        />
      </div>

      <div class="form--text__input">
        <label class="form--counter__label">Город</label>
        <vue-text-input
          class="form--component__input"
          v-model="registrationInfo.userCounter.counter"
          :disabled="true"
        />
        <label class="form--date__label-error" ref="warningLabel"
          >На данный момент мы работаем только в этом регионе!</label
        >
      </div>
    </vue-form>

    <div class="registration-page__widget" v-if="loginType === 'registration'">
      <vue-widget class="widget">
        <div class="widget__offer">
          <p>С <span @click="goToPage('offer')">офертой</span> ознакомлен</p>
          <vue-check-box
            :value="checkboxState"
            @change="updateStateCheckBox($event)"
          />
        </div>
        <vue-button-text :title="'Отправить'" />
      </vue-widget>
    </div>

    <vue-form v-if="loginType === 'login'" class="registration-page__form">
      <div class="form--number__input">
        <label class="form--number__label">Номер телефона</label>
        <vue-number-input
          v-model="loginInfo.userPhone.phone.value"
          v-model:is-valid="loginInfo.userPhone.isValid.value"
          :type="'phone'"
          :placeholder="'+7 (###)-###-##-##'"
        />
      </div>
      <div class="form--number__input">
        <label class="form--number__label">СМС КОД</label>
        <vue-number-input
          v-model="loginInfo.userSMS.sms.value"
          v-model:is-valid="loginInfo.userSMS.isValid.value"
          :placeholder="'1234'"
        />
      </div>
      <div
        class="form--button__post"
        v-if="loginInfo.userSMS.sms.value === null"
      >
        <vue-button-text
          :class="{ confirm_state: refConfirmBtnState === true }"
          v-if="!isTimerActive"
          :title="postSmsCode"
          @click="sendSmsCode"
        />

        <vue-timer
          v-else
          :initialTime="10"
          @timer-complete="
            isTimerActive = !isTimerActive;
            hasTimerExpired = !hasTimerExpired;
          "
        />
      </div>
      <div
        class="form--button__confirm"
        v-if="loginInfo.userSMS.sms.value !== null"
      >
        <vue-button-text
          :class="{ confirm_state: refConfirmBtnState === true }"
          :title="titleConfirmBtn"
          @click="confirmSms(loginInfo.userSMS.sms.value)"
        />
      </div>
    </vue-form>
  </div>
</template>
<script lang="ts" setup>
// vue
import { useRoute, useRouter } from "vue-router";
import { ref, computed, watch } from "vue";
//components
import VueNumberInput from "@/components/Inputs/VueNumberInput.vue";
import VueForm from "@/components/Forms/VueForm.vue";
import VueTextInput from "@/components/Inputs/VueTextInput.vue";
import VueModalSwitcher from "@/components/Modals/VueModalSwitcher.vue";
import VueSwitcher from "@/components/Switchers/VueSwitcher.vue";
import VueWidget from "@/components/Widgets/VueWidget.vue";
import VueCheckBox from "@/components/CheckBoxs/VueCheckBox.vue";
import VueButtonText from "@/components/Buttons/VueButtonText.vue";
import VueTimer from "@/components/Timer/VueTimer.vue";
//interfaces and types
import { RegistrationInfoType } from "@/types/RegistrationInfoType";
import { SwitcherParamsType } from "@/types/SwitcherParamsType";

const loginType = ref("registration"); //для входа login

const selectTypeRegistration = (val: string) => {
  if (val === "registration") {
    loginType.value = "login";
    resetFormQueryParams(); // Сбрасываем параметры формы при переключении
  } else if (val === "login") {
    loginType.value = "registration";
    resetFormQueryParams(); // Сбрасываем параметры формы при переключении
  }
};

const resetFormQueryParams = () => {
  const query = { ...route.query }; // Копируем текущие query параметры

  // Удаляем параметры, связанные с формой, но сохраняем `root`
  const formParams = [
    "name",
    "firstName",
    "serName",
    "phone",
    "whts",
    "date",
    "ch",
    "birthdate",
  ];
  formParams.forEach((param) => {
    if (query[param]) {
      delete query[param];
    }
  });

  // Обновляем URL без параметров формы
  router.push({ query });
};

const userRole = ref<0 | 1>(1);

const switcherRoleParams = ref<SwitcherParamsType[]>([
  {
    id: 1,
    value: "Грузчик",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Грузоперевозчик",
    isSwitch: false,
  },
  {
    id: 3,
    value: "Заказчик",
    isSwitch: false,
  },
]);

watch(
  switcherRoleParams,
  (newRoles) => {
    const selectedRole = newRoles.find((role) => role.isSwitch);
    if (selectedRole) {
      userRole.value = selectedRole.value === "Заказчик" ? 0 : 1;
    }
  },
  { deep: true }
);

const switcherQuestionParams = ref<SwitcherParamsType[]>([
  {
    id: 1,
    value: "Да",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Нет",
    isSwitch: false,
  },
]);

const loginInfo = {
  userPhone: {
    phone: ref(null),
    isValid: ref(null),
  },
  userSMS: {
    sms: ref(null),
    isValid: ref(null),
  },
};

const isTimerActive = ref(false);
const hasTimerExpired = ref(false);

const postSmsCode = computed(() =>
  hasTimerExpired.value ? "Запросить код ещё раз!" : "Запросить код"
);

const sendSmsCode = () => {
  isTimerActive.value = true;
  hasTimerExpired.value = false;
};

const refConfirmBtnState = ref(false);

const titleConfirmBtn = ref("Подтвердить");

const confirmSms = (val: string | number) => {
  refConfirmBtnState.value = !refConfirmBtnState.value;
  titleConfirmBtn.value = "Проверка";
  setTimeout(() => {
    refConfirmBtnState.value = !refConfirmBtnState.value;
    titleConfirmBtn.value = "Подтвержден!";
    setTimeout(() => {
      router.push("/");
    }, 1000);
  }, 2000);
};

const router = useRouter();
const route = useRoute();

const setQueryParams = (type: string, value: string | number | null): void => {
  router.push({
    query: {
      ...route.query,
      [type]: value,
    },
  });
};

const checkboxState = ref(false);

const updateStateCheckBox = (ev: boolean) => {
  checkboxState.value = ev;
  setQueryParams("ch", checkboxState.value.toString());
};

const registrationInfo: RegistrationInfoType = {
  userPhone: {
    phone: ref(null),
    isValid: ref(false),
  },
  userWhatsapp: {
    phone: ref(null),
    isValid: ref(false),
  },
  userName: {
    name: ref(""),
    isValid: ref(false),
  },
  userSerName: {
    serName: ref(""),
    isValid: ref(false),
  },
  userFirstName: {
    firstName: ref(""),
    isValid: ref(false),
  },
  userDate: {
    date: ref(""),
    isValid: ref(false),
  },
  userCounter: {
    counter: "Владивосток, пригород",
  },
};

// отслеживаем значения и записываем их в query параметры!
watch(
  () => registrationInfo.userName.name.value,
  (newVal) => setQueryParams("name", newVal)
);
watch(
  () => registrationInfo.userFirstName.firstName.value,
  (newVal) => setQueryParams("firstName", newVal)
);
watch(
  () => registrationInfo.userSerName.serName.value,
  (newVal) => setQueryParams("serName", newVal)
);
watch(
  () => registrationInfo.userPhone.phone.value,
  (newVal) => setQueryParams("phone", newVal)
);
watch(
  () => registrationInfo.userWhatsapp.phone.value,
  (newVal) => setQueryParams("whts", newVal)
);
watch(
  () => registrationInfo.userDate.date.value,
  (newVal) => setQueryParams("date", newVal)
);

// инициализируем параметры
if (route.query) {
  const queryParams = route.query;

  if (queryParams.root === "true") {
    switcherQuestionParams.value[0].isSwitch = true;
  } else if (queryParams.root === "false") {
    switcherQuestionParams.value[0].isSwitch = false;
  }

  if (queryParams.ch) {
    checkboxState.value = queryParams.ch === "true";
  }

  if (queryParams.name) {
    registrationInfo.userName.name.value = queryParams.name as string;
  }
  if (queryParams.firstName) {
    registrationInfo.userFirstName.firstName.value =
      queryParams.firstName as string;
  }
  if (queryParams.serName) {
    registrationInfo.userSerName.serName.value = queryParams.serName as string;
  }
  if (queryParams.phone) {
    registrationInfo.userPhone.phone.value = queryParams.phone as string;
  }
  if (queryParams.whts) {
    registrationInfo.userWhatsapp.phone.value = queryParams.whts as string;
  }
  if (queryParams.date) {
    registrationInfo.userDate.date.value = queryParams.date as string;
  }
}

const goToPage = (path: string) => {
  router.push({
    path: path,
    query: {
      ...route.query,
      reg: "true",
    },
  });
};
</script>

