<template>
  <div v-if="isOpen" class="modal-overlay">
    <div class="modal-content">
      <slot></slot>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref, defineProps, watch } from "vue";
import { SwitcherParamsType } from "@/types/SwitcherParamsType";
import { useRouter } from "vue-router";

const props = defineProps<{
  value: SwitcherParamsType[];
}>();

const router = useRouter();

const isOpen = ref(true);

const openModal = () => {
  isOpen.value = true;
};

const closeModal = () => {
  isOpen.value = false;
};

watch(
  () => props.value,
  () => {
    if (props.value[0].isSwitch) {
      closeModal();
      setRootQueryParams(true);
    } else {
      setRootQueryParams(false);
    }
  },
  { deep: true }
);

if (props.value[0].isSwitch) {
  closeModal();
}

const setRootQueryParams = (valid: boolean) => {
  router.push({
    query: {
      ...router.currentRoute.value.query,
      root: valid.toString(),
    },
  });
};
</script>

