@import url(../../styles/variable.css);
.modal {
  &-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--yellow);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  &-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 500px;
